/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Form, Badge } from "react-bootstrap";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";

/**
  * User imports
  ============================================================================ */
import Card from "../card";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Balance" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { userData } = props.barrelGetters;
  const { setAccountType, setRedirectTo } = props.barrelSetters;
  const { format } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const userDataParsed = userData;

  const available =
    userDataParsed !== null
      ? format(
          userDataParsed.acc_curr +
            userDataParsed.acc_cred +
            userDataParsed.acc_save
        )
      : null;
  let acc_curr =
    userDataParsed !== null ? format(userDataParsed.acc_curr) : null;
  let acc_cred =
    userDataParsed !== null ? format(userDataParsed.acc_cred) : null;
  let acc_save =
    userDataParsed !== null ? format(userDataParsed.acc_save) : null;

  /**
    * Functions
    ============================================================================ */

  /**
   *
   * @param {*} type
   */
  const onAccountTypeSelected = (type) => {
    setAccountType(type);
    setRedirectTo("/transfer");
  };

  const getJSX = () => {
    return (
      <Card
        backgroundHeader="Transparent"
        backgroundBody="Transparent"
        border="0"
        header={false}
        maxWidthBody="700px"
      >
        <Form noValidate>
          <Form.Group>
            <Form.Label>{t("Available")}</Form.Label>

            <Form.Text>
              <div style={{ fontSize: "3em" }}>{available}</div>
            </Form.Text>
          </Form.Group>
          <Form.Group>
            <ul className="list-group">
              <li className="list-group-item d-flex justify-content-between align-items-center">
                <div style={{}}>{t("Current account")}</div>
                <span className="" onClick={() => {}}>
                  <Badge variant="Light">{acc_curr}</Badge>
                </span>
              </li>
              <li className="list-group-item d-flex justify-content-between align-items-center">
                <div style={{}}>{t("Credit account")}</div>

                <span className="" onClick={() => {}}>
                  <Badge variant="Light">{acc_cred}</Badge>
                </span>
              </li>
              <li className="list-group-item d-flex justify-content-between align-items-center">
                <div style={{}}>{t("Savings account")}</div>
                <span className="" onClick={() => {}}>
                  <Badge variant="Light">{acc_save}</Badge>
                </span>
              </li>
            </ul>
          </Form.Group>
        </Form>
      </Card>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
