/**
 * CSS
 ============================================================================ */
import "./Toasts.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";

/**
    * User imports
    ============================================================================ */
import LanguageSelector from "../language-selector";
import FileLoader from "../fileLoader";

/**
 * Default export
 */
export default (args) => {
  /**
      * Default Properties
      ============================================================================ */
  let defaults = { namespace: "Toasts" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
      * Barrels
      ============================================================================ */
  const { signedIn, language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
      * Destructured vars
      ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";
  // const appClass = "App";

  /**
      * Functions
      ============================================================================ */
  const getJSX = () => {
    let fileName;
    switch (window.location.pathname) {
      case "/":
      case "/sign-in":
        fileName = "ToastHome.js";
        break;

      case "/sign-up":
        fileName = "ToastSignUp.js";
        break;

      case "/auth":
        fileName = "ToastAuth.js";
        break;

      // case "/transfer":
      //   fileName = "Transfer.js";
      //   break;

      // case "/transfer/u2s":
      //   fileName = "TransferU2S.js";
      //   break;

      // case "/transfer/u2u":
      //   fileName = "TransferU2U.js";
      //   break;

      // case "/transfer/u2b":
      //   fileName = "TransferU2B.js";
      //   break;

      default:
        fileName = "_ToastDefault.js";
        break;
    }

    return (
      <div
        className={appClass}
        style={{
          padding: "0 0% 0 0%",
        }}
      >
        {/**
         * Wrapper for the main window
         */}
        <div className={wrapperClass} style={{}}>
          {/** overflowY: "auto", maxHeight: "100vh" */}
          {/**
           * Top margin of the main window (only for >= sm)
           */}
          <div className="d-none d-md-block" style={{ height: "1rem" }}></div>
          {/**
           * The main window
           */}
          <div
            className=""
            style={{
              background: "Transparent",
              boxShadow: "none",
              padding: "0 15px",
            }}
          >
            <FileLoader
              {...args}
              file={fileName}
              path3="toasts"
              defaultFile="_ToastDefault.js"
            />

            {/**
             * Footer
             */}
            {false && (
              <div className="text-center">
                <span style={{}}>
                  <LanguageSelector {...args} />
                </span>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  };

  /**
      * Return
      ============================================================================ */
  return <>{getJSX()}</>;
};
