import React from "react";
import { useTranslation } from "react-i18next";
import { Build } from "@material-ui/icons";
import { Container, Row, Col } from "react-bootstrap";

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "UnderConstruction" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  return (
    <>
      <Container fluid>
        <Row>
          {/** Left column */}
          <Col xl={4} lg={6} className="d-none d-lg-block" style={{}}></Col>

          {/** Middle column */}
          <Col
            lg={6}
            md={12}
            style={{ padding: "40px 0 0 0", backgroundColor: "Transparent" }}
          >
            <center>
              <h3>
                <Build style={{ color: "var(--dark)", fontSize: "3rem" }} />
                <br />
                <br />
                {t("We are constructing")}
                <br />
                <br />
              </h3>
            </center>
          </Col>

          {/** Right column */}
          <Col
            className="d-none d-xl-block"
            style={{ paddingLeft: 0, paddingRight: 0 }}
          ></Col>
        </Row>
      </Container>
    </>
  );
};
