/**
 * System imports
 ============================================================================ */
import React, { useRef } from "react";
import { useTranslation } from "react-i18next";
import { FileCopy as FileCopyIcon } from "@material-ui/icons";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Component", value: "" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { copyToClipboard } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const refCopyableField = useRef(null);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return (
      <>
        <span
          style={{
            position: "relative",
            display: "block",
            overflow: "auto",
            // wordBreak: "break-all",
            // whiteSpace: "nowrap",
          }}
        >
          <input
            ref={refCopyableField}
            className="no-focus"
            readOnly
            style={{
              border: 0,
              width: "10rem",
              background: "Transparent",
            }}
            type="text"
            defaultValue={props.value}
          />

          {document.queryCommandSupported("copy") && (
            <span style={{ float: "right", marginLeft: "0.25rem" }}>
              <button
                className="button-transparent"
                onClick={() => copyToClipboard(refCopyableField)}
              >
                <FileCopyIcon style={{ color: "var(--dark)" }} />
              </button>
            </span>
          )}
        </span>
      </>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
